.displayGrid {
}
.displayTable {
}
.Ticker.displayTable {
  display: grid;
  grid-template-columns: 1fr 3fr;
}
.Ticker {
  background-color: var(--color-3);
  border-top: 1px solid var(--color-5);
  border-left: 1px solid var(--color-5);
}
.Ticker:hover {
  margin-left: -5px;
  margin-top: -5px;
  -webkit-box-shadow: 5px 5px var(--color-shadow);
          box-shadow: 5px 5px var(--color-shadow);
}
.Ticker.gridTable {
  display: grid;
  grid-template-columns: 1fr 3fr;
}
.TickerHeader {
  background-color: var(--color-4);
  padding: 20px 20px 10px 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.TickerHeader.displayTable {
  padding: 10px 20px;
}
.TickerHeaderName {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
}
.TickerHeaderName.displayTable {
  display: grid;
  grid-template-columns: 20px 1fr;
  width: 100%;
}
.TickerHeaderName.displayTable p {
  grid-column: 2/3;
}
.TickerHeaderName * {
  margin-right: 2px;
}
.TickerHeaderNameIcon {
  background-color: white;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  -webkit-box-shadow: 1px 1px var(--color-shadow);
          box-shadow: 1px 1px var(--color-shadow);
}
.TickerHeaderNameSymbol {
  font-weight: 400;
}
.TickerContent {
  padding: 10px 15px 20px 15px;
  display: grid;
}
.TickerContent.displayTable {
  grid-template-columns: 1fr 1fr;
  padding: 10px 20px;
}
.up{
  color:lightgreen;
}
.down{
  color:lightcoral;
}


